@import '../config.all';

.image-logo {
  position: absolute;
  left: 3rem;
}

.menu__link {
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
  margin-top: 26px;
}

.menu__link-li {
  margin-right: 20px;
}

.menu__link-a {
  font-family: $font-primary;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  letter-spacing: 0.1em;
  text-decoration: none;
  color: #000;
}

.client__container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.client__studio {
  margin-left: 120px;
}
.client__title {
  font-family: $font-primary;
  font-size: 40px;
  font-weight: 400;
  line-height: 49px;
  margin-bottom: 40px;
  color: #000;
}

.client__description {
  font-family: $font-primary;
  font-size: 16px;
  line-height: 25px;
  font-weight: 400;
  margin-bottom: 33px;
  color: #565656;
}

.client__link {
  a {
    font-family: $font-primary;
    background: #2639ed;
    padding: 20px 40px;
    border-radius: 60px;
    color: #fff;
    text-decoration: none;
  }
}

.orclient__container {
  display: flex;
  margin: 0 120px 0 120px;
}

.orclient__title {
  font-family: $font-primary;
  font-size: 40px;
  font-weight: 400;
  line-height: 49px;
  color: #000;
  margin: 0;
}

.orclient__descrip {
  font-family: $font-primary;
  font-size: 18px;
  font-weight: 400;
  line-height: 28px;
  color: #757575;
}

.orclient-images {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 49px;
}

.orclient-img {
  margin-left: 37px;
  margin-right: 30px;
}
